/* eslint-disable @typescript-eslint/no-explicit-any */
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, DestroyRef, OnInit, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NgForOf, NgIf, NgSwitch, NgSwitchCase } from '@angular/common';
import { NzTypographyModule } from 'ng-zorro-antd/typography';
import { NzPageHeaderModule } from 'ng-zorro-antd/page-header';
import { NzSpaceModule } from 'ng-zorro-antd/space';
import { NzTabsModule } from 'ng-zorro-antd/tabs';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { IComLibsServicesProjectAdminUserService } from "@icom/services/projects";
import { ActivatedRoute, Router } from '@angular/router';
import { IProduct } from '@icom/interfaces';
import { lastValueFrom } from 'rxjs';
import { cloneDeep } from 'lodash';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
@Component({
  selector: 'icom-libs-admin-user-history-detail',
  templateUrl: './detail.component.html',
  standalone: true,
  changeDetection: ChangeDetectionStrategy.OnPush,
  imports: [
    NgxDatatableModule,
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NzFormModule,
    NgForOf,
    NgSwitch,
    NgSwitchCase,
    NzTypographyModule,
    NzPageHeaderModule,
    NzSpaceModule,
    NzTabsModule,
    NzIconModule,
    NgIf,
  ],
  styles: [
    `
      :host {
        background: #f5f5f5;
        overflow: hidden;
        padding: 24px;
        display: block;
      }

      .card-container ::ng-deep p {
        margin: 0;
      }
      .card-container ::ng-deep > .ant-tabs-card .ant-tabs-content > .ant-tabs-tabpane {
        background: #fff;
        padding: 16px;
      }
      .card-container ::ng-deep > .ant-tabs-card > .ant-tabs-nav::before {
        display: none;
      }
      .card-container ::ng-deep > .ant-tabs-card .ant-tabs-tab {
        border-color: transparent;
        background: transparent;
      }
      .card-container ::ng-deep > .ant-tabs-card .ant-tabs-tab-active {
        border-color: #fff;
        background: #fff;
      }
    `
  ]
})

export class IcomLibsProductDetailComponent implements OnInit {
  protected product: IProduct | undefined;
  protected productTemp: any | undefined;
  protected productId: number;

  fields: FormlyFieldConfig[] = [];
  fieldsDevice: FormlyFieldConfig[] = [];
  protected model: any | undefined;
  form = new FormGroup({});
  options: FormlyFormOptions = {
    formState: {
      disabled: false,
    },
  };

  protected productService = inject(IComLibsServicesProjectAdminUserService);
  protected cdr = inject(ChangeDetectorRef);
  protected route = inject(ActivatedRoute);
  protected router = inject(Router);
  protected destroyRef = inject(DestroyRef);

  constructor(private routeActive: ActivatedRoute) {
    this.productId = this.routeActive.snapshot.params['id'];
    this.fields = [
      {
        fieldGroupClassName: 'flex flex-col',
        className: 'basis-1/2',
        key: '',
        wrappers: ['group'],
        props: {
          label: 'Thông tin chung',
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
        fieldGroup: [
          {
            key: 'full_name',
            type: 'input-custom',
            props: {
              label: 'Hội viên',
              type: 'string',
              readonly: true
            },
          },
          {
            key: 'username',
            type: 'input-custom',
            props: {
              label: 'Số điện thoại',
              type: 'string',
              readonly: true
            },
          },
          {
            key: 'feature_text',
            type: 'input-custom',
            props: {
              label: 'Tính năng',
              type: 'string',
              readonly: true
            },
          },
          {
            key: 'created_at',
            type: 'date', // Sử dụng trường kiểu tùy chỉnh 'range-picker'
            props: {
              readonly: true,
              label: 'Thời gian',
              ShowTime: true,
              format: 'dd-MM-yyyy HH:mm',
            },
          },
          {
            key: 'action_text',
            type: 'input-custom',
            props: {
              label: 'Thao tác',
              type: 'string',
              readonly: true
            },
          },
        ]
      },
      {
        fieldGroupClassName: 'flex flex-col',
        className: 'basis-1/2',
        key: '',
        wrappers: ['group'],
        props: {
          label: 'Nội dung',
        },
        expressions: {
          'props.disabled': 'formState.disabled',
        },
        fieldGroup: [
          {
            key: 'content',
            type: 'input-custom',
            props: {
              type: 'string',
              readonly: true
            },
          },
          {
            key: 'policy_content',
            type: 'drawer-link',
            templateOptions: {
              label: 'Chính sách bảo mật',
            },
            hideExpression: (field: any) => {
              return !field?.policy_content;
            },
          },
          {
            key: 'term_of_use',
            type: 'drawer-link',
            templateOptions: {
              label: 'Điều khoản chung',
            },
            hideExpression: (field: any) => {
              return !field?.term_of_use;
            },
          }
        ]
      }
    ];
    this.fieldsDevice = [
      {
        key: 'ip_address',
        type: 'input-custom',
        props: {
          label: 'Địa chỉ IP',
          type: 'string',
          readonly: true
        },
      },
      {
        key: 'device',
        type: 'input-custom',
        props: {
          label: 'Thiết bị',
          type: 'string',
          readonly: true
        },
      },
      {
        key: 'operating_system',
        type: 'input-custom',
        props: {
          label: 'Hệ điều hành',
          type: 'string',
          readonly: true
        },
      },
    ]
  }

  async ngOnInit() {
    this.route.queryParams
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(async (params) => {
        try {
          const result = await lastValueFrom(this.productService.getDetailHistory(params.id));
          this.model = cloneDeep(result.data);
          this.cdr.detectChanges();
        } catch (err) {
          console.log(err);
        }
      })
  }
}
